import { Component, ViewChild } from "@angular/core";
import { LoadingController } from 'ionic-angular';

import { MyHeardePage } from '../my_hearde.component';
declare var AMap;
@Component({
    templateUrl: "geolocation.component.html"
})

export class geolocationPage {
    position: any;
    position_location: any;
    constructor(
        public Loading: LoadingController
    ) {
    }

    getlocation() {
        // let loader = this.Loading.create({
        //     content: "...",
        // });
        // loader.present();
        this.position = new AMap.Map('container', {
            zoom: 16,
        });
        let loadCtrl = this.Loading
        let position_location_r = this.position_location
        let position_r = this.position
        this.position.plugin('AMap.Geolocation', function () {
            position_location_r = new AMap.Geolocation({
                enableHighAccuracy: true,//:true
                timeout: 10000,          //10
                maximumAge: 0,           //00
                convert: true,           //true
                showButton: true,        //true
                GeoLocationFirst: true,   //ip
                buttonPosition: 'LB',    //'LB'
                buttonOffset: new AMap.Pixel(0, 0),//Pixel(10, 20)
                showMarker: true,        //true
                showCircle: true,        //true
                panToLocation: true,     //true
                useNative: true,          //sdk
                zoomToAccuracy: true      //false
            });
            position_r.addControl(position_location_r);
            position_location_r.getCurrentPosition(function (status, result) {
                console.log(result)
                if (result.status != 0) {
                    var markers = [];   //provincesDemoJS
                    let marker = new AMap.Marker({
                        position: [result.position.lng, result.position.lat],
                        offset: new AMap.Pixel(0, 0),
                        map: position_r
                    });
                    markers.push(marker);
                } else {
                    let loader = loadCtrl.create({
                        content: "",
                        duration: 2000
                    });
                    loader.present();
                }
                //    loader.dismiss();
            });
            AMap.event.addListener(position_location_r, 'complete');//
            AMap.event.addListener(position_location_r, 'error');      //
        });
    }

    ionViewDidEnter() {
        this.getlocation()

    }
}


